import {version} from '../package.json'
const ErrorTypes = {
  network: {
    code: 1,
    msg: '',
    remark: 'videoxhr'
  },
  mse: {
    code: 2,
    msg: '',
    remark: ''
  },
  parse: {
    code: 3,
    msg: '',
    remark: 'mp4hlsflvjs'
  },
  format: {
    code: 4,
    msg: '',
    remark: ''
  },
  decoder: {
    code: 5,
    msg: '',
    remark: ''
  },
  runtime: {
    code: 6,
    msg: '',
    remark: ''
  },
  timeout: {
    code: 7,
    msg: '',
    remark: ''
  },
  other: {
    code: 8,
    msg: '',
    remark: ''
  }
}

class Errors {
  static get ErrorTypes() {
    return ErrorTypes
  }
  constructor (type, currentTime, duration, networkState, readyState, src, currentSrc,
  ended, errd = {line: '', handle: '', msg: '', version: ''}) {
    let r = {}
    if (arguments.length >= 3 && arguments.length < 5) {
      r.playerVersion = version // 
      r.errorType = arguments[0]
      r.code = arguments[1]
      r.errd = arguments[2] || {}// 
      r.ex = (r.errd || {}).msg // 
      r.domain = document.domain // domain
    } else {
      r.playerVersion = version // 
      r.errorType = type
      r.domain = document.domain // domain
      r.duration = duration // 
      r.currentTime = currentTime
      r.networkState = networkState
      r.readyState = readyState
      r.currentSrc = currentSrc
      r.src = src
      r.ended = ended
      r.errd = errd || {}// 
      r.ex = (ErrorTypes[type] || {}).msg // 
      r.code = r.errd.code
    }
    
    return r
  }
}

export default Errors